import React, {Component} from 'react';
import { Grid, Icon } from 'semantic-ui-react';

import { Container } from 'semantic-ui-react'
import {EVENT_START_TIME, EVENT_END_TIME} from "../constants";

// const PRESENTATION_SCHEDULE_NUM_COLUMNS = 4;

const scheduleItems = [
  {
    title: "Intro to Git",
    date: "Saturday 12:30pm - 1:00pm",
    icon: 'star'
  },
  {
    title: "Rapid Deployment for Hackathons by Nathan Doraty",
    date: "Saturday 1:00pm - 2:00pm",
    icon: 'star'
  },
  {
    title: "Best Practices for Deploying ML by Luke Kumar (Amii)",
    date: "Saturday 2:00pm - 3:00pm",
    icon: 'star'
  },
  {
    title: "How to Deploy Code on AWS by Greg and Garry (Intuit)",
    date: "Saturday 3:00pm - 4:00pm",
    icon: 'star'
  },
  {
    title: "Vue-ing Open Data by Mitch Wong (Darkhorse Analytics)",
    date: "Saturday 4:00pm - 5:00pm",
    icon: 'star'
  },
  {
    title: "End-to-End IoT, IFTTT+MQTT; Internet->WiFi->BLE by Steven Knudsen",
    date: "Saturday 5:00pm - 6:00pm",
    icon: 'star'
  },
  {
    title: "Exploiting Top Web Vulnerabilities by Dorsa Nahid and Alireza Hafez",
    date: "Saturday 7:00pm - 8:00pm",
    icon: 'star'
  },
];

export default class PresSchedule extends Component {
  renderScheduleItem(item){
    if (item.fullDate){
      return <li className="list-group-item text-white bg-primary" key={item.fullDate}><b>{item.fullDate}</b></li>;
    }
    else if (item.description && item.location){
      return (
        <li className="list-group-item" key={item.title}>
          <Grid columns={2} divided>
            <Grid.Column mobile={3} tablet={3} computer={1}>
              <Icon fitted size="big" name={item.icon}/>
            </Grid.Column>
            <Grid.Column mobile={13} tablet={13} computer={15}>
              <b>{item.title}</b>
              <p>{item.date}</p>
              <p>{item.description}</p>
              <p className="bg-primary">{item.location}</p>
            </Grid.Column>
          </Grid>
        </li>
      );
    }
    else if (item.description){
      return (
        <li className="list-group-item" key={item.title}>
          <Grid columns={2} divided>
            <Grid.Column mobile={3} tablet={3} computer={1}>
              <Icon fitted size="big" name={item.icon}/>
            </Grid.Column>
            <Grid.Column mobile={13} tablet={13} computer={15}>
              <b>{item.title}</b>
              <p>{item.date}</p>
              <p>{item.description}</p>
            </Grid.Column>
          </Grid>
        </li>
      );
    }
    else if (item.location){
      return (
        <li className="list-group-item" key={item.title}>
          <Grid columns={2} divided>
            <Grid.Column mobile={3} tablet={3} computer={1}>
              <Icon fitted size="big" name={item.icon}/>
            </Grid.Column>
            <Grid.Column mobile={13} tablet={13} computer={15}>
              <b>{item.title}</b>
              <p>{item.date}</p>
              <p className="text-white">{item.location}</p>
            </Grid.Column>
          </Grid>
        </li>
      );
    }
    else{
      return (
        <li className="list-group-item" key={item.title}>
          <Grid columns={2} divided>
            <Grid.Column mobile={3} tablet={3} computer={1}>
              <Icon fitted size="big" name={item.icon}/>
            </Grid.Column>
            <Grid.Column mobile={13} tablet={13} computer={15}>
              <b>{item.title}</b>
              <p>{item.date}</p>
            </Grid.Column>
          </Grid>
        </li>
      );
    }
  }

  render() {
    return (
      <div>
        <Grid id="Schedule" style={{
          margin: 0,
          paddingBottom: 40,
          width:"100%"
        }}
              stackable
              relaxed='very'
              centered>
        </Grid>
        <Container>
          <br/>
          <h1 style={{textAlign: 'center'}}>WORKSHOP SCHEDULE</h1>
          <br/>
          <ul className="list-group">
            {scheduleItems.map(item => this.renderScheduleItem(item))}
          </ul>
        </Container>
      </div>
    );
  }
}